@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0;padding:0}

.App{font-family:'Roboto', sans-serif;min-height:100vh;background:#d3cce3;background:linear-gradient(to bottom, #e9e4f0, #d3cce3)}.App .AppContainer{max-width:1200px;margin-left:auto;margin-right:auto}.App .AppContainer h1{text-align:center;padding:24px}

.searchProcesses{display:flex;justify-content:center;flex-wrap:wrap}.searchProcesses button{background:#ffffff;font-size:16px;text-transform:capitalize;border:0;margin:4px;padding:5px}.searchProcesses button:hover{background:#b6b1c0}.searchProcesses button:focus{outline:none;background:#b6b1c0}

.searchTool{margin-top:24px}.searchTool p{margin:0 auto 8px;text-align:center;width:250px;font-size:20px}.searchTool .searchToolButton{display:flex;justify-content:center;flex-wrap:wrap}.searchTool .searchToolButton button{background:#ffffff;font-size:16px;text-transform:capitalize;border:0;margin:4px;padding:5px}.searchTool .searchToolButton button:hover{background:#b6b1c0}.searchTool .searchToolButton button:focus{outline:none;background:#b6b1c0}

.resultCards{display:flex;flex-wrap:wrap;justify-content:center}

.pokemonCard{width:135px;height:200px;padding:4px;margin:16px;background:#ffffff;box-shadow:0 6.6px 10px rgba(0,0,0,0.046),0 10px 80px rgba(0,0,0,0.07);display:flex;flex-direction:column;justify-content:center;align-items:center}.cardHeader{text-transform:capitalize}

